<template>
  <div class="myHouse">
    <navigation-bar  @onLeftClick="goBack" pageName="我的发布" :navBarStyle="navStyle" leftImgType="1"/>
    <van-tabs v-model="active" :border="false" title-active-color="#fb5768" line-width="50%" color="#fb5768" v-if="!networkError">
        <van-tab title="我发布的房源">
            <div class="box">
                <van-pull-refresh v-model="isRefresh" @refresh="refresh()">
                    <div v-if="houseList.length > 0">
                        <div class="block"></div>
                        <van-swipe-cell v-for="(item, index) in houseList" :key="index">
                            <div class="item" @click="goDetail(item.HOUSE_CODE)">
                                <div class="left">
                                    <img :src="item.PIC_URL ? item.PIC_URL : defaultIcon" alt="">
                                </div>
                                <div class="middle">
                                    <div class="title">{{item.TITLE}}</div>
                                    <div class="bottom">
                                        <div class="area">{{item.AREA}}</div>
                                        <div class="room-box">
                                            <div class="room">{{item.ROOM_HALL}}</div>
                                            <div class="price">{{item.PRICE}}{{item.HOUSE_FLAG == '1' ? "元/月" : "万"}}</div>
                                        </div>
                                    </div>
                                </div>
                                <div class="status">
                                    <img :src="getStatusIcon(item.STATUS)" alt="">
                                </div>
                            </div>
                            <div class="right-slot" slot="right">
                                <div class="edit" @click="editHouse(0, item)" v-show="item.STATUS == 0 || item.STATUS == 2">修改</div>
                                <div class="del" @click="deleteHouse(item.HOUSE_CODE)">删除</div>
                            </div>
                        </van-swipe-cell>
                    </div>
                    <div class="noData" v-if="houseList.length == 0">
                        <img :src="noDataIcon" alt="">
                        <div>暂无房源</div>
                    </div>
                </van-pull-refresh>
            </div>
        </van-tab>
        <van-tab title="我发布的车位">
            <div class="box">
                <van-pull-refresh v-model="isRefresh" @refresh="refresh()">
                    <div v-if="parkingList.length > 0">
                        <div class="block"></div>
                        <van-swipe-cell v-for="(item, index) in parkingList" :key="index">
                            <div class="item" @click="goDetail(item.HOUSE_CODE)">
                                <div class="left">
                                    <img :src="item.PIC_URL ? item.PIC_URL : defaultIcon" alt="">
                                </div>
                                <div class="middle">
                                    <div class="title">{{item.TITLE}}</div>
                                    <div class="bottom">
                                        <div class="area">{{item.AREA}}</div>
                                        <div class="room-box">
                                            <div class="room">{{item.PARK_NO}}</div>
                                            <div class="price">{{item.PRICE}}{{item.HOUSE_FLAG == '1' ? "元/月" : "万"}}</div>
                                        </div>
                                    </div>
                                </div>
                                <div class="status">
                                    <img :src="getStatusIcon(item.STATUS)" alt="">
                                </div>
                            </div>
                            <div class="right-slot" slot="right">
                                <div class="edit" @click="editHouse(1, item)" v-show="item.STATUS == 0 || item.STATUS == 2">修改</div>
                                <div class="del" @click="deleteHouse(item.HOUSE_CODE)">删除</div>
                            </div>
                        </van-swipe-cell>
                    </div>
                    <div class="noData" v-if="parkingList.length == 0">
                        <img :src="noDataIcon" alt="">
                        <div>暂无车位</div>
                    </div>
                </van-pull-refresh>
            </div>
        </van-tab>
    </van-tabs> 
    <div style="height:300px;padding-top: 50px;" v-if="networkError">
      <NetworkError>
        <div slot="refreshMode" class="refresh-btn" @click="getMyHouseList()">
          重新加载
        </div>
      </NetworkError>
    </div>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import {
  getMyHouseService,
  delHouseService
} from "../../../services/houseRentalService";

import leftIcon from "@/assets/images/common/nav_return_white.png";
import noDataIcon from "@/assets/images/home/notice/img_default@2x.png";
import defaultIcon from "@/assets/images/home/houseRental/bannerHouse.jpg";
import sellingIcon from "@/assets/images/home/houseRental/selling.png";
import sellOutIcon from "@/assets/images/home/houseRental/sellOut.png";
import rentingIcon from "@/assets/images/home/houseRental/renting.png";
import rentOutIcon from "@/assets/images/home/houseRental/rentOut.png";
import NetworkError from '../../../common/networkError/NetworkError';
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "myHouse",
  data() {
    return {
      leftIcon: leftIcon,
      noDataIcon: noDataIcon,
      defaultIcon: defaultIcon,
      statusList: [sellingIcon, sellOutIcon, rentingIcon, rentOutIcon],
      active: 0,
      houseList: [],
      parkingList: [],
      isRefresh: false,
      scrollTop: 0,
      networkError: false,
      navStyle: {
        "background": "rgb(251, 87, 104)",
        "color": "#ffffff"
      }
    };
  },
  activated() {
    this.networkError = false;
    if (this.$store.state.houseRental.reloadMine) {
      this.reset();
      this.$store.dispatch("setReloadMine", false);
      document.querySelector(".box").scrollTop = 0;
      this.active = this.$store.state.houseRental.mineActive;
      this.getMyHouseList();
    } else {
      document.querySelector(".box").scrollTop = this.scrollTop;
    }
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelector(".box").scrollTop;
    next();
  },
  components: {
    NetworkError: NetworkError,
    NavigationBar
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.reset();
      this.$router.go(-1);
    },

    reset() {
      (this.active = 0), (this.scrollTop = 0);
      this.houseList = [];
      this.parkingList = [];
    },

    /**
     * 获取状态icon
     */
    getStatusIcon(status) {
      return this.statusList[status];
    },

    /**
     * 切换列表
     */
    changeList(tab) {
      this.activeTab = tab;
      this.$store.dispatch("setMineActive", tab);
      this.getMyHouseList();
    },

    getMyHouseList() {
      getMyHouseService({
        data: JSON.stringify({
          USER_CODE: this.$userInfo.OWNER_CODE
        })
      }).then(
        result => {
          this.networkError = false;
          this.isRefresh = false;
          this.houseList = result.HOUSE_LIST;
          this.parkingList = result.PARKSPACE_LIST;
        },
        error => {
          this.networkError = true;
          this.isRefresh = false;
          this.$toast(error);
        }
      );
    },

    /**
     * 下拉刷新
     */
    refresh() {
      this.getMyHouseList();
    },

    /**
     * 删除操作
     */
    deleteHouse(code) {
      Dialog.confirm({
        title: "",
        message: "确认删除？"
      })
        .then(() => {
          this.delete(code);
        })
        .catch(() => {});
    },

    /**
     * 调用接口删除
     */
    delete(code) {
      var data = {
        HOUSE_CODE: code,
        UESR_CODE: "2018122909483465412" //this.$userInfo.OWNER_CODE,
      };
      delHouseService({
        data: JSON.stringify(data)
      }).then(
        res => {
          if (res.success) {
            this.getMyHouseList();
            this.$toast("删除成功");
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 编辑操作
     */
    editHouse(flag, item) {
      var param = {
        whichId: item.HOUSE_CODE,
        whichType: item.CAR_LOCATION_FLAG
      };
      this.$router.push({
        name: flag ? "editCar" : "editHouse",
        params: param
      });
    },

    /**
     * 跳转到详情页面
     */
    goDetail(HOUSE_CODE) {
      this.$router.push({
        name: "houseDetail",
        params: {
          HouseCode: HOUSE_CODE
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.myHouse {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .box {
    height: 100%;
    overflow: scroll;
    .block {
      width: 100%;
      height: 20px;
    }
    .item {
      width: 100%;
      height: 102px;
      padding: 12px;
      box-sizing: border-box;
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border-top: 1px solid #f2f2f2;
      .left {
        width: 102px;
        height: 72px;
        img {
          width: 102px;
          height: 72px;
        }
      }
      .middle {
        width: 228px;
        margin-left: 20px;
        height: 78px;
        padding-right: 10px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-direction: column;
        .title {
          width: 100%;
          font-size: 14px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
        }
        .bottom {
          width: 100%;
          .area {
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-size: 14px;
            line-height: 20px;
            color: gray;
          }
          .room-box {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            line-height: 20px;
            .room {
              width: 100px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-size: 14px;
              line-height: 20px;
              color: gray;
            }
            .price {
              color: #ff481f;
              font-weight: 700;
              font-size: 14px;
            }
          }
        }
      }
      .status {
        width: 42px;
        height: 42px;
        position: absolute;
        right: 0;
        top: 0;
        img {
          width: 42px;
          height: 42px;
        }
      }
    }
    .right-slot {
      height: 102px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      font-size: 16px;
      .edit {
        background-color: #11c1f3;
        color: #fff;
        width: 56px;
        height: 102px;
        line-height: 102px;
        text-align: center;
      }
      .del {
        background-color: #ef473a;
        color: #fff;
        width: 56px;
        height: 102px;
        line-height: 102px;
        text-align: center;
      }
    }
  }
  .noData {
    padding-top: 147px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    img {
      width: 132px;
      height: 132px;
    }
    div {
      font-size: 14px;
      color: #c4c4c4;
      text-align: center;
    }
  }
  /deep/.van-tabs {
    flex: 1;
    position: relative;
  }
  /deep/.van-tabs__wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  /deep/.van-tabs__content {
    position: absolute;
    top: 44px;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  /deep/.van-tab__pane {
    height: 100%;
  }
  /deep/.van-pull-refresh {
    min-height: 100%;
  }
  /deep/.van-pull-refresh__track {
    min-height: 100%;
  }
  .refresh-btn {
    border: 1px solid #B5B5B5;
    width: 125px;
    border-radius: 2rem;
    height: 40px;
    min-height: 30px !important;
    line-height: 40px;
    color: #444;
    margin: 0 auto;
    font-size: 16px;
    margin-top: 10px;
  }
  /deep/.network {
    background: #ffffff;
  }
}
</style>
